import React, { useEffect } from "react";
/* import { Link } from "react-router-dom"; */
import ReactModal from "react-modal";
import "../scss/Phone.scss";
import { useStore } from "../store-zustand/store";
import { Tooltip } from "react-tippy";
import "react-tippy/dist/tippy.css";
import Background from "../img-main/jalusi1.jpg";
const customStyles = {
  content: {
    position: "absolute",
    top: "-20px",
    left: "0",
    width: "100vw",
    height: "110vh",
    zIndex: "998",
    /*     backgroundColor: "Black", */
    backgroundImage: `url(${Background})`,
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    opacity: "1",
    overflow: "hidden",
    cursor: "pointer",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    textAline: "center",
  },
};
const color1 = "#343336";
const color2 = "#B408A4";

const myStyles = {
  position: "absolute",
  top: "90vh",
  fontSize: "20px",
  cursor: "pointer",
  fontWeight: "500",

  background: `linear-gradient(	90deg,
	rgba(228, 157, 25, 0.986) 0%,
	rgb(255, 94, 0) 100%)`,
  borderRadius: "8px",
  padding: "3px 13px",
};
const myStylesCLose = {
  position: "absolute",
  top: "0px",
  right: "5px",
  fontSize: "20px",
  cursor: "pointer",
  fontWeight: "500",
};

export default function MainModal() {
  const ModalChange = useStore((state) => state.ModalChange);
  const modalmain = useStore((state) => state.modalmain);
  const [modalIsOpen, setIsOpen] = React.useState(false);

  useEffect(() => {
    console.log(`modalmain`, modalmain);
    openModal();
    return () => {};
  }, []);

  function openModal() {
    setIsOpen(true);
  }

  function afterOpenModal() {}

  function closeModal() {
    setIsOpen(false);
  }
  var subtitle;

  return (
    <div style={{ cursor: "pointer" }} onClick={closeModal}>
      <ReactModal
        closeTimeoutMS={5000}
        ariaHideApp={false}
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        {/*       <div className="fon "></div> */}
        <div style={myStylesCLose}>
          <span onClick={closeModal}>x</span>
        </div>
        <a style={myStyles} onClick={closeModal}>
          Зайти на сайт
        </a>
      </ReactModal>
    </div>
  );
}
