import React, { useState, useEffect } from "react";
import { FiChevronRight, FiChevronLeft } from "react-icons/fi";

import "../../scss/Slider.scss";
import {
  LazyLoadComponent,
  LazyLoadImage,
  trackWindowScroll,
} from "react-lazy-load-image-component";
import { useStore } from "../../store-zustand/store";

function SliderCustom({ scrollPosition }) {
  const data1 = useStore((state) => state.data1);
  const [people, setPeople] = useState(data1);
  const [index, setIndex] = React.useState(0);

  useEffect(() => {
    const lastIndex = people.length - 1;
    if (index < 0) {
      setIndex(lastIndex);
    }
    if (index > lastIndex) {
      setIndex(0);
    }
  }, [index, people]);

  useEffect(() => {
    let slider = setInterval(() => {
      setIndex(index + 1);
    }, 5000);
    return () => {
      clearInterval(slider);
    };
  }, [index]);

  return (
    <LazyLoadComponent className="section-slider">
      <div className="section-center">
        {people.map((person, personIndex) => {
          const { id, image, name, title, quote } = person;

          let position = "nextSlide";
          if (personIndex === index) {
            position = "activeSlide";
          }
          if (
            personIndex === index - 1 ||
            (index === 0 && personIndex === people.length - 1)
          ) {
            position = "lastSlide";
          }

          return (
            <article className={position} key={id}>
              <div
                className="person-right"
                onClick={() => setIndex(index + 1)}
              ></div>
              <div
                className="person-left"
                onClick={() => setIndex(index - 1)}
              ></div>
              <img src={image} alt={name} className="person-img" />
              {/*               <LazyLoadImage
                className="person-img"
                effect="black-and-white"
                alt={name}
                src={image}
                placeholder={<div class="lds-dual-ring"></div>}
              /> */}
              <h4>{name}</h4>
              <p className="title">{title}</p>
              <p className="text">{quote}</p>
            </article>
          );
        })}
        <button className="prev" onClick={() => setIndex(index - 1)}>
          <FiChevronLeft />
        </button>
        <button className="next" onClick={() => setIndex(index + 1)}>
          <FiChevronRight />
        </button>
      </div>
    </LazyLoadComponent>
  );
}

export default trackWindowScroll(SliderCustom);
