import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import logo2 from "../../img-main/logoSun.png";
import { useStore } from "../../store-zustand/store";
import "../../scss/HeaderInfo.scss";
import TFDrop from "./TF-drop";
import LogoCustom from "../Logo";
/* 	изминение теми */
const getStorageTheme = () => {
  let theme = "light-theme";
  if (localStorage.getItem("theme")) {
    theme = localStorage.getItem("theme");
  }
  return theme;
};
/* 	изминение теми */
export default function HeaderInfo() {
  /* 	изминение теми */
  const [theme, setTheme] = useState(getStorageTheme());

  const toggleTheme = () => {
    if (theme === "light-theme") {
      setTheme("dark-theme");
    } else {
      setTheme("light-theme");
    }
  };

  useEffect(() => {
    document.documentElement.className = theme;
    localStorage.setItem("theme", theme);
  }, [theme]);
  /* 	изминение теми */
  const ModalChange = useStore((state) => state.ModalChange);
  const modalOpen = useStore((state) => state.modalOpen);
  function openModal() {
    ModalChange(modalOpen);
  }
  return (
    <div className="navbar">
      <div className="nav-center">
        <div className="header__menu menu">
          <nav className="menu__body">
            <ul className="menu__list">
              <li></li>
              <li>
                <a className="menu__link __goto-block">Щодня з 9-00 до 21-00</a>
              </li>
              <li>
                <div className="list-icon-header">
                  <a
                    href="https://www.facebook.com/Lviv-Sunrise-%D1%81%D0%BE%D0%BD%D1%86%D0%B5%D0%B7%D0%B0%D1%85%D0%B8%D1%81%D0%BD%D1%96-%D1%81%D0%B8%D1%81%D1%82%D0%B5%D0%BC%D0%B8-106791861548674/events/?ref=page_internal"
                    className="list-icon__one"
                  ></a>
                  <a className="list-icon__two"></a>
                  <a className="list-icon__three"></a>
                  <a className="list-icon__foure"></a>
                </div>
              </li>
              <li>
                <TFDrop />
              </li>
              <li>
                <a href="#lots" className="_goto-block"></a>
              </li>
              <li>
                <a className="menu__link goto-block ">
                  <div className=" flash lighting"></div>
                  <span onClick={openModal}>Звяжіться з нами</span>
                </a>
              </li>
              <li>
                {" "}
                <button className="btn-them" onClick={toggleTheme}></button>
              </li>
            </ul>
          </nav>
        </div>
      </div>
    </div>
  );
}

const Style = {
  padding: "7px 0px 0px 0px",
  width: "140px",
  height: "85px",
};
