import React, { useState, useRef, useEffect } from "react";
import { FaBars } from "react-icons/fa";
/* import logo from "../../../img-main/logoSun.png"; */
import logo from "../../../img-main/логотип-круг.jpg";
import { NavLink } from "react-router-dom";
import "../../../scss/NavbarCustom.scss";
import LogoCustom from "../../Logo";

const Navbar = () => {
  const [showLinks, setShowLinks] = useState(false);
  const linksContainerRef = useRef(null);
  const linksRef = useRef(null);
  const toggleLinks = () => {
    setShowLinks(!showLinks);
  };
  useEffect(() => {
    const linksHeight = linksRef.current.getBoundingClientRect().height;
    if (showLinks) {
      linksContainerRef.current.style.height = `${linksHeight}px`;
    } else {
      linksContainerRef.current.style.height = "0px";
    }
  }, [showLinks]);

  return (
    <div className="container-main">
      <div className="navbar-center">
        <div className="navbar-header">
          <NavLink to="/main">
            {/*           <img src={logo} className="logo" alt="logo" />  */}
            <LogoCustom />
          </NavLink>
          <button className="navbar-toggle" onClick={toggleLinks}>
            <FaBars />
          </button>
        </div>
        <div className="navlinks-container" ref={linksContainerRef}>
          <ul className="navlinks" ref={linksRef}>
            <li>
              <NavLink
                className={"nav-link"}
                activeClassName={"nav-selected"}
                to="/main"
              >
                Головна
              </NavLink>
            </li>
            <li>
              <NavLink
                className={"nav-link"}
                activeClassName={"nav-selected"}
                to="/about"
              >
                Про нас
              </NavLink>
            </li>
            <li>
              <NavLink
                className={"nav-link"}
                activeClassName={"nav-selected"}
                to="/work"
              >
                Наші проекти
              </NavLink>
            </li>
            <li>
              <NavLink
                className={"nav-link"}
                activeClassName={"nav-selected"}
                to="/order"
              >
                Замовлення
              </NavLink>
            </li>
            {/* 
            <li>
              <NavLink className={"nav-link"} activeClassName={"nav-selected"} to="/test">
                Test
              </NavLink>
            </li> */}
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Navbar;
